<template>
	<view style="width: 100%;">
		<view style="background: linear-gradient(to top right,#77f7de,#4ddaf0);height: 100rpx;width: 100%;display: flex;flex-direction: column-reverse;">
			<view style="display: flex;flex-direction: row;justify-content: center;margin-bottom: 5px;">
				<!-- <image src="../../static/login/jiantou.png" style="width: 10px;" mode="widthFix"></image> -->
				<text style="color: #fff;">登录</text>
				
			</view>
		</view>
		<view style="padding: 10px;display: flex;flex-direction: column;">
			<input style="height: 20px;" disabled="true"/>
			<text style="font-size: 30px;color: #11b3a6;font-weight: bold;">密码登录</text>
			
			<text style="font-size: 14px;color: #a0d9d5;margin-top: 50rpx;font-weight: bold;">用户名</text>
			<!-- <view style="width: ;"></view> -->
			<input v-model="phone" style="background-color: #f9f9f9;border-radius: 10px;height: 40px;margin-top: 5px;"/>
			
			<view style="display: flex;flex-direction: row;align-items: center;">
				<text style="font-size: 14px;color: #a0d9d5;margin-top: 50rpx;font-weight: bold;">密码</text>
				<text style="font-size: 12px;color: #d1d1d1;margin-top: 50rpx;">(密码必须8位数字或字母)</text>
			</view>
			
			<!-- <view style="width: ;"></view> -->
			<input password="true" v-model="pwd" style="background-color: #f9f9f9;border-radius: 10px;height: 40px;margin-top: 5px;"/>
			
			<view style="display: flex;flex-direction: row;margin-top: 20px;width: 100%;align-items: center;justify-content: space-between;">
				<view @click="login()" style="background: linear-gradient(to top right,#4ddaf0,#77f7de);width: 250rpx;height: 40px;display: flex;flex-direction: column;justify-content: center;border-radius: 20px;text-align: center;">
					<text style="color: #fff;">登录</text>
				</view>
				
				<text @click="jump_pwd()" style="color: #7fccc6;">忘记密码</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone:'',
				pwd:''
			}
		},
		mounted() {
			uni.setNavigationBarColor({
				backgroundColor:'#77f7de'
			})
		},
		methods: {
			jump_pwd(){
				uni.navigateTo({
					url:'/pages/login/forget'
				})
			},
			login(){
				var that = this;
				if (!that.phone) {
					uni.showToast({ title: '请输入手机号', icon: 'none' });
					return;
				}
				if (!that.pwd) {
					uni.showToast({ title: '请输入密码', icon: 'none' });
					return;
				}
				uni.request({
					url:this.server_url+'/loginbyPassword?user='+this.phone+'&password='+this.pwd,
					method:'GET',
					success: (res) => {
						if(res.data.result=="登录成功"){
							//token,user保存
								uni.showToast({
									icon:'success',
									title:'登录成功'
								});
								//保存token,user
								uni.setStorage({
									key:'token',
									data:res.data.token
								});
								uni.setStorage({
									key:'user',
									data:this.phone
								});
								uni.setStorageSync('gender',res.data.gender);
								//判断是不是管理员
								if(this.phone=='admin'){
									uni.setStorage({
										key:'admin',
										data:true
									});
								}
								
								uni.switchTab({
									url:'../navigation/ShouYe/ShouYe'
								})
							}else{
								uni.showToast({
									icon:'error',
									position:'bottom',
									title:res.data.result
								});
								this.pwd="";
							}
					}
				});
			}
		}
	}
</script>

